<template>
  <div>
    <LayOut>
      <template #content>
        <a-form-model layout="inline">
          <a-form-model-item label="受助人姓名">
            <a-input v-model="real_name"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-button @click="onSubmit" type="primary" html-type="submit">查询</a-button>
          </a-form-model-item>
        </a-form-model>
        <a-divider></a-divider>
        <a-table
          :columns="columns"
          :pagination="pagination"
          :data-source="data"
          :scroll="{ x: 1500, y: 300 }"
        >
          <span slot="action" slot-scope="record">
            <router-link :to="{path:'/updatedemand',query:{real_name:record.real_name}}">修改</router-link>
            <a-divider type="vertical" />
            <a-popconfirm
              title="您确定要删除吗?"
              ok-text="Yes"
              cancel-text="No"
              @confirm="confirm(record.real_name)"
              @cancel="cancel"
            >
              <a>删除</a>
            </a-popconfirm>
          </span>
        </a-table>
      </template>
    </LayOut>
  </div>
</template>
<script>
import { queryDemandByName } from "@/api/api";
import { delDemand } from "@/api/api";
import { initDemands } from "@/api/api";
import LayOut from "@/views/LayOut.vue";
const columns = [
  {
    dataIndex: "real_name",
    key: "real_name",
    title: "姓名",
    width: 80
  },
  {
    title: "监护人姓名",
    dataIndex: "guardianName",
    key: "guardianName",
    width: 120
  },
  {
    title: "监护人电话",
    dataIndex: "guardianPhone",
    key: "guardianPhone"
  },
  {
    title: "监护人身份证",
    key: "guardianID",
    dataIndex: "guardianID"
  },
  {
    title: "家属户",
    key: "isFamily",
    dataIndex: "isFamily",
    width: 80
  },
  {
    title: "疾病史",
    key: "illness",
    dataIndex: "illness",
    width: 80
  },
  {
    title: "服务开始日期",
    key: "startDate",
    dataIndex: "startDate"
  },
  {
    title: "服务截止日期",
    key: "endDate",
    dataIndex: "endDate"
  },
  {
    title: "服务内容",
    key: "serverItem",
    dataIndex: "serverItem"
  },
  {
    title: "备注",
    key: "specification",
    dataIndex: "specification"
  },
  {
    title: "操作",
    key: "action",
    scopedSlots: { customRender: "action" }
  }
];

const data = [];
export default {
  data() {
    return {
      real_name: "",
      pagination: {
        total: 0,
        pageSize: 10, //每页中显示10条数据
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "50", "100"], //每页中显示的数据
        showTotal: total => `共有 ${total} 条数据` //分页中显示总的数据
      },
      data,
      columns,
      formInline: {
        real_name: "",
        idCard: "",
        assistantName: "",
        disabledContact: "",
        userType: "",
        district: ""
      }
    };
  },
  async mounted() {
    let res = await initDemands();
    for (let i = 0; i < res.length; i++) {
      if (res[i].startDate)
        res[i].startDate = this.$moment(res[i].startDate).format("YYYY-MM-DD");
      if (res[i].endDate)
        res[i].endDate = this.$moment(res[i].endDate).format("YYYY-MM-DD");
    }
    this.data = res;
  },
  methods: {
    async onSubmit(e) {
      let res = await queryDemandByName(this.real_name);
      console.log(res);
      this.data = res;
    },
    async confirm(e) {
      let res = await delDemand(e);
      if (res.data.errmsg == "ok") {
        this.$message.success("删除成功");
        let res = await initDemands();
        this.data = res;
      }
    },
    cancel(e) {
      // this.$message.error("点击 No");
    }
  },
  components: {
    LayOut
  }
};
</script>

